body {
  margin: 0;
  padding: 0;
}
.slide {
  width: 100vw;
  height: 100vh;
}
.wrapper {
  display: flex;
  flex-direction: row;
  width: 500%;
  transform: rotate(90deg) translateY(-100vh);
  transform-origin: top left;
  
}
.one {
  background: url("background.png");
  background-size: auto 100%;
  /* background-repeat: repeat-x; */
  width: 300%;
}
.outer-wrapper {
  width: 100vh;
  height: 100vw;
  transform: rotate(-90deg) translateX(-100vh);
  transform-origin: top left;
  overflow-y: scroll;
  overflow-x: hidden;
  position: absolute;
  scrollbar-width: none;
  -ms-overflow-style: none;
}
::-webkit-scrollbar {
  display:none;
}
.cloud{
  width: 40vh;
  position: absolute;
  top: 10%;
  z-index: 3;
}
.cloud2{
  width: 40vh;
  position: absolute;
  left: 70%;
  z-index: 3;
}
.my_name_fp{
  width: 23%;
  position: absolute;
  top: 10%;
  left: 7%;
  z-index: 5;
}
.my_character{
  height: 18%;
  position: absolute;
  bottom: 6%;
  left: 48%;
  z-index: 5;
}
